.Logo {
  margin-top: 25px;
  margin-bottom: 20px;

  display: flex;
  justify-content: center;
}

.Logo .LogoImage {
  width: 50px;
}

.Logo .LogoText {
  margin-left: 10px;
  font: 25px ui-rounded;
  line-height: 25px;
}

.Header nav {
  text-align: center;
}

.Header nav ul {
  list-style-type: none;
  padding-inline-start: 0;
}

.Header nav ul li {
  display: inline-block;
}

.HeaderLink {
  display: inline-block;

  color: white;

  border: 0.18em solid #2a7361;
  border-radius: 0.7em;
  padding: 0.5em;
  margin: 0.2em;
  background: linear-gradient(178deg, #00e1e1, darkcyan);
  text-decoration: none;
  text-shadow: 0.1em 0.1em 0.2em black;
  box-shadow: inset 0.1em 0.1em 0.4em 0.1em #dbe9f5;
}

.HeaderLink:active,
.HeaderLink.active,
.HeaderLink:hover {
  color: white;
  border: 0.18em solid white;
  background: linear-gradient(178deg, #f3d332, #df6b39);
  box-shadow: inset 0.1em 0.1em 0.4em 0.1em #ebcabb, 0em 0em 0.4em 0.1em #ebcabb;
}
